import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,
  name1: PropTypes.string,
  name2: PropTypes.string,
});

export const formValues = PropTypes.shape({
  id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  name1: PropTypes.string,
  name2: PropTypes.string,
});

export const formTouched = PropTypes.shape({
  id: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  name1: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  name2: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  id: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  name1: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  name2: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
