import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/point-2-ds';

export const apiPoint2DGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiPoint2DPost = async (serviceUrl, point2D) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: point2D ? JSON.stringify(point2D) : null,
  };
  return request(url, options);
};

export const apiPoint2DPut = async (serviceUrl, id, point2D) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: point2D ? JSON.stringify(point2D) : null,
  };
  return request(url, options);
};

export const apiPoint2DDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
